import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home'
import Room from '@/pages/room'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/room',
      component: Room
    }
  ]
})



